@import url('https://fonts.googleapis.com/css?family=Eczar|Montserrat');
.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
}
body{
  background-image:url(https://www.desktopbackground.org/p/2015/01/08/883788_background-images-for-websites-professional-gray_1550x1033_h.jpg);
  font-family: 'Montserrat', sans-serif;

}
.edit {
  height: 20vh;
  width: 80vw;
  border: 3px solid black;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
#editHead{
  width: 80vw;
  text-align:center;
  background-color: rgb(206, 220, 239);
  height:3vh;
  line-height: 3vh;
}
#previewHead{
  width: 80vw;
  text-align:center;
  background:-webkit-radial-gradient(rgb(196, 196, 196),rgb(204, 211, 224),rgb(206, 220, 239)) ;
  background:-o-radial-gradient(rgb(196, 196, 196),rgb(204, 211, 224),rgb(206, 220, 239)) ;
  background:radial-gradient(rgb(196, 196, 196),rgb(204, 211, 224),rgb(206, 220, 239)) ;
  height:3vh;
  line-height: 3vh;
  border-bottom:1px solid black;
}
h3{
  font-family: 'Eczar', serif;
  font-size: 3vh;
  margin:0;
}
#editor {
  -ms-flex:1 1;
      flex:1 1;
  background-color: rgb(206, 220, 239);
}
.previewer {
  width: 80vw;
  border: 3px solid black;
  margin: auto;
  background:-webkit-radial-gradient(rgb(204, 204, 215),rgb(206, 220, 239)) ;
  background:-o-radial-gradient(rgb(204, 204, 215),rgb(206, 220, 239)) ;
  background:radial-gradient(rgb(204, 204, 215),rgb(206, 220, 239)) ;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



